.input-field {
  width: 6.5em;

  input {
    width: 4em;
    padding: 0.5em;
    border-radius: 0px;
    border-color: transparent transparent #a8a8a8 transparent;
    text-align: center;
    border-width: 2px;
    &:focus {
      border-color: transparent transparent rgb(105, 105, 105) transparent;
      border-radius: 0px;
      outline: none;
    }
  }

  .label {
    margin-left: 0.5em;
  }
}

.min-max {
  width: 3em;
}

.variable-list {
  text-align: right;
}

.wrong-input,
.wrong-input:focus {
  border-bottom-color: rgb(240, 56, 56) !important;
}

.input-row {
  padding: 0.5em;
  button, select, label, input {
    margin: 0em 0.4em;
  }
  button {
    padding: 0.5em; 
  }
  select {
    padding: 0.4em;
    width: 16em;
  }
  input {
    padding: 0.4em;
    width: 6em;
  }
}

#input-table {
  td {
    height: 2em;
  }
}