@import './variables';

.multi-pane {
  margin-top: 1em;
  margin-bottom: 1em;
}

.pane-list {

}

.pane-button {
  display: inline;
  user-select: none;
  float: left;
  cursor: pointer;
  padding: 0.7em;
  border-radius: 0.5em;
  &:hover {
    background-color: rgb(246, 246, 246);
  }
}

.pane-button-selected {
  background-color: rgb(240, 240, 240);
  &:hover {
    background-color: rgb(235, 235, 235);
  }
}

.pane {
  padding: 5px;
  overflow-x: auto;
}

.pane-hidden {
  display: none
}

.split-pane {
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.split {
  width: 50%;
  padding: 5px 15px;
}

.split-border {
  width: 0px;
  border-left: 1px solid $split-border-color;
  margin: 30px 0px;
}

.clear-fix {
  clear: both;
}