@import './variables';

.tableau-box {
  overflow-x: auto;
  width: 100%;
}

.tableau-table {
  border-collapse: collapse;
  margin: 1em auto;
  user-select: none;

  tr {
    padding: none;
    margin: none;
    border: none;
    height: 36px;
  }

  td {
    /* border-bottom: 1px solid rgb(231, 231, 231); */
    margin: none;
    padding: 5px;
    text-align: center;
    width: 4em;
    margin: 0.3em;
  }
}

.cell-corner {
  background-color: $corner-color;
}

.cell-top-left,
.cell-top-right,
.cell-top {
  border-bottom: $tableau-inner-border-width solid 
}

.cell-bottom-left,
.cell-bottom-right,
.cell-bottom {
  border-top: $tableau-inner-border-width solid $tableau-inner-border-color;
}

.cell-top-left,
.cell-bottom-left,
.cell-left {
  border-right: $tableau-inner-border-width solid $tableau-inner-border-color;
}

.cell-top-right,
.cell-bottom-right,
.cell-right {
  border-left: $tableau-inner-border-width solid $tableau-inner-border-color;
}



.tableau-cell {
  background-color: white;
}

.not-selectable-cell {
  background-color: $disabled-color;
}

.secondary-cell {
  background-color: $secondary-selection-color;
}

.selected-cell {
  background-color: $selection-color;
}

.hidden-cell {
  display: none;
}

.highlighted-cell {
  background-color: $pivot-light;
}

.disabled-cell {
  background-color: $disabled-color;
}

.clickable-cell {
  cursor: pointer;
}

.invariant-holds {
  background-color: $positive-light;
  border-color: $positive-dark;
  &.not-selectable-cell {
    background-color: mix($positive-light, $not-selectable-color, 25%);
    border-color: mix($positive-dark, $not-selectable-color, 25%);
  }
  &.secondary-cell {
    background-color: mix($positive-light, $secondary-selection-color, 25%);
    border-color: mix($positive-dark, $secondary-selection-color, 25%);
  }
  &.selected-cell {
    background-color: mix($positive-light, $selection-color, 25%);
    border-color: mix($positive-dark, $selection-color, 25%);
  }  
}

.invariant-broken {
  background-color: $negative-light;
  border-color: $negative-dark;
  &.not-selectable-cell {
    background-color: mix($negative-light, $not-selectable-color, 25%);
    border-color: mix($negative-dark, $not-selectable-color, 25%);
  }
  &.secondary-cell {
    background-color: mix($negative-light, $secondary-selection-color, 25%);
    border-color: mix($negative-dark, $secondary-selection-color, 25%);
  }
  &.selected-cell {
    background-color: mix($negative-light, $selection-color, 25%);
    border-color: mix($negative-dark, $selection-color, 25%);
  }
}

.base-cell {
  background-color: $base-light;
  // border-color: $base-dark;
  &.not-selectable-cell {
    background-color: mix($base-light, $not-selectable-color, 25%);
  }
  &.secondary-cell {
    background-color: mix($base-light, $secondary-selection-color, 25%);
  }
  &.selected-cell {
    background-color: mix($base-light, $selection-color, 25%);
  }
}

.solution-table {
  border-collapse: collapse;
  margin: auto;

  td {
    padding: 0.5em;
    text-align: center;
    border-right: 1px solid $split-border-color;
    &:last-child {
      border-right: none;
    }
  }

  tr {
    &:first-child td {
      border-bottom: 1px solid $split-border-color;
    } 
  }
}

.solution {
  .solution-header {
    font-size: 1.2em;
    display: inline-block;
    margin-bottom: 0.5em;
    border-bottom: 1px solid lightgray;
    padding: 0.3em;
  }
  .highlight-pos, .highlight-neg {
    margin-left: 2em;
    font-size: 0.9em;
    font-weight: bold;
  }
  .highlight-pos {
    color: $positive-dark;
  }
  .highlight-neg {
    color: $negative-dark;
  }
  .inner {
    margin: 1em;
    overflow-x: auto;
  }
}

#controls {
  display: inline-block;
}

#counter {
  margin: 1em;
}

#controls button {
  width: 6em;
  padding: 0.5em;
}

#tab-and-controls {
  margin-top: 2em;
  text-align: center;
}

#message {
  /* height: 3em; */
  padding: 1em;
}

#inner-wrapper {
  width: 800px;
  position: relative;
}

#sidebar {
  width: 300px;
  position: absolute;
  left: calc(100% + 15px);
  top: 0;
}

#settings {
  border-radius: 20px;
  overflow: hidden;
}

#start-over {
  margin-top: 1em;
  width: 100%;
}

.setting-tab {
  // border-radius: 20px;
  padding: 15px;
  // margin: 10px;
  user-select: none;
  .header {
    font-size: 1.2em;
    border-width: 0px 0px 1px 0px;
    border-style: solid;
    padding-bottom: 5px;
    padding-left: 10px;
  }
}

.setting-pivot {
  @extend .setting-tab;
  background-color: $pivot-dark;
  color: white;
  .header {
    border-color: $pivot-light;
  }
}

.setting-base {
  @extend .setting-tab;
  background-color: $base-dark;
  color: white;
  .header {
    border-color: $base-light;
  }
}

.setting-other {
  @extend .setting-tab;
  background-color: $positive-dark;
  color: white;
  .header {
    border-color: $positive-light;
  }
}

.setting {
  padding: 1em 0.5em 0em;
  display: flex;
  align-items: center;
  // .description {
  //   float: left;
  // }
  .switch {
    margin-right: 1em;
  }
}

.setting-select {
  width: calc(100% - 1em);
  padding: 0.5em;
  border-radius: 0.6em;
  background-color: white;
}

.clear-fix {
  clear: both;
}