@import './variables';

body {
  background-color: rgb(250, 250, 250);
  font-family:Arial, Helvetica, sans-serif;
}

#wrapper {
  width: 1100px;
  margin: auto;
  padding: 20px;
  overflow: visible;
  // position: relative;
}

label, button {
  user-select: none;
}

button {
  padding: 0.7em;
  background-color: $button;
  color: white;
  border-radius: 5px;
  border: 1px solid $button-border;
  font-weight: bold;
  box-shadow: none;
  cursor: pointer;
  transition: 0.25s ease;
  &:hover {
    background-color: $button-hover;
  }
  &:disabled {
    background-color: $button-disabled;
    border-color: $button-disabled-border;
    cursor: default;
  }
}

.inline-button {
  margin: 0 5px;
}